.pot-swipe {
  overflow: hidden;
}

.pot-swipe-enter-active,
.pot-swipe-leave-active {
  transition: all 0.3s;
}

.pot-swipe-enter-active .pot-swipe-item-inner,
.pot-swipe-leave-active .pot-swipe-item-inner {
  transition: all 0.3s;
}

.pot-swipe-enter .pot-swipe-item-inner,
.pot-swipe-leave-to .pot-swipe-item-inner {
  height: 0;
}

