.pot-scroll-nav {
  position: relative;
  height: 100%;
  overflow: hidden;
}

.pot-scroll-nav .pot-sticky-content {
  height: 100%;
}

.pot-scroll-nav-main {
  overflow: hidden;
}

.pot-scroll-nav-main > .pot-sticky {
  position: static;
}

.pot-scroll-nav_side > .pot-sticky {
  display: flex;
}

.pot-scroll-nav_side > .pot-sticky > .pot-scroll-wrapper {
  flex: 1;
}

.pot-scroll-nav_side > .pot-sticky > .pot-sticky-fixed {
  position: relative;
  height: 100%;
  order: -1;
}

.pot-scroll-nav_side .pot-scroll-nav-main > .pot-sticky-ele {
  float: left;
}

.pot-scroll-nav_side .pot-scroll-nav-main > .pot-sticky {
  overflow: hidden;
}

