.pot-image-preview-fade-enter,
.pot-image-preview-fade-leave-active {
  opacity: 0;
}

.pot-image-preview-fade-enter-active,
.pot-image-preview-fade-leave-active {
  transition: all 0.3s ease-in-out;
}

.pot-image-preview .pot-popup-mask {
  opacity: 0.6;
}

.pot-image-preview .pot-popup-content {
  width: 100%;
  height: 100%;
}

.pot-image-preview .pot-slide-item {
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.pot-image-preview-container {
  height: 100%;
  margin: 0 -0.625rem;
}

.pot-image-preview-header,
.pot-image-preview-footer {
  position: absolute;
  left: 0;
  right: 0;
}

.pot-image-preview-header {
  top: 0;
}

.pot-image-preview-footer {
  bottom: 0;
}

.pot-image-preview-counter {
  position: absolute;
  bottom: 3.125rem;
  width: 100%;
  text-align: center;
  font-size: 0.875rem;
  color: #fff;
}

.pot-image-preview-item {
  position: relative;
  padding: 0 0.625rem;
  width: 100%;
  height: 100%;
}

.pot-image-preview-item .pot-scroll-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}

.pot-image-preview-item .pot-image-preview-img {
  display: block;
  height: auto;
  max-width: 100%;
  max-height: 100%;
}

