.pot-tab-bar {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.pot-tab-bar_inline .pot-tab {
  display: flex;
  align-content: center;
  justify-content: center;
}

.pot-tab-bar-slider {
  position: absolute;
  left: 0;
  bottom: 0;
  height: 0.125rem;
  width: 1.25rem;
  background-color: #e8864c;
}

