.pot-checkbox-group {
  z-index: 1;
  overflow: hidden;
  background-color: #fff;
}

.pot-checkbox-group .pot-checkbox:last-child .pot-checkbox-wrap::before {
  display: none;
}

.pot-checkbox-group .pot-checkbox:last-child .pot-checkbox-wrap::after {
  display: none;
}

.pot-checkbox-group[data-horz="true"] {
  display: flex;
  padding-left: 0;
  position: relative;
  border-radius: 0.125rem;
}

.pot-checkbox-group[data-horz="true"]::after {
  content: "";
  pointer-events: none;
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  transform-origin: 0 0;
  border: 1PX solid #ccc;
  border-radius: 0.125rem;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
}

@media (min-resolution: 2dppx) {
  .pot-checkbox-group[data-horz="true"]::after {
    width: 200%;
    height: 200%;
    border-radius: 0.25rem;
    transform: scale(0.5);
  }
}

@media (min-resolution: 3dppx) {
  .pot-checkbox-group[data-horz="true"]::after {
    width: 300%;
    height: 300%;
    border-radius: 0.375rem;
    transform: scale(0.333);
  }
}

.pot-checkbox-group[data-horz="true"] .pot-checkbox {
  flex: 1;
  flex-basis: 1e-0.5625rem;
  width: 1%;
  text-align: center;
  padding-left: 0.625rem;
  padding-right: 0.625rem;
}

.pot-checkbox-group[data-horz="true"] .pot-checkbox:after {
  border-color: #ccc;
}

.pot-checkbox-group[data-horz="true"] .pot-checkbox:last-child::before {
  display: none;
}

.pot-checkbox-group[data-horz="true"] .pot-checkbox:last-child::after {
  display: none;
}

.pot-checkbox-group[data-horz="true"] .pot-checkbox[data-pos="right"] .pot-checkbox-ui {
  position: relative;
  margin-left: 0.42em;
  order: 1;
}

.pot-checkbox-group[data-horz="true"] .pot-checkbox[data-pos="right"] .pot-checkbox-label {
  margin-right: 0;
}

.pot-checkbox-group[data-horz="true"] .pot-checkbox-wrap {
  justify-content: center;
}

