.pot-scroll-nav-bar {
  position: relative;
  display: flex;
  color: #666;
  background-color: #fff;
}

.pot-scroll-nav-bar_horizontal {
  align-items: center;
}

.pot-scroll-nav-bar_horizontal .pot-scroll-wrapper {
  flex: 1;
  text-align: center;
}

.pot-scroll-nav-bar_horizontal .pot-scroll-content {
  display: inline-block;
}

.pot-scroll-nav-bar_horizontal .pot-scroll-nav-bar-items {
  white-space: nowrap;
}

.pot-scroll-nav-bar_vertical {
  height: 100%;
  justify-content: center;
  text-align: center;
}

.pot-scroll-nav-bar_vertical .pot-scroll-nav-bar-item {
  display: block;
}

.pot-scroll-nav-bar-items {
  font-size: 0.875rem;
}

.pot-scroll-nav-bar-item {
  display: inline-block;
  padding: 1.25rem 0.9375rem;
}

.pot-scroll-nav-bar-item_active {
  color: #fc9153;
}

