.pot-checker-item {
  display: inline-block;
  vertical-align: top;
  text-align: center;
  padding: 0.5rem 0.625rem;
  margin-right: 0.625rem;
  color: #666;
  background: #fff;
  border-radius: 0.25rem;
  position: relative;
}

.pot-checker-item::after {
  content: "";
  pointer-events: none;
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  transform-origin: 0 0;
  border: 1PX solid #fcfcfc;
  border-radius: 0.25rem;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
}

@media (min-resolution: 2dppx) {
  .pot-checker-item::after {
    width: 200%;
    height: 200%;
    border-radius: 0.5rem;
    transform: scale(0.5);
  }
}

@media (min-resolution: 3dppx) {
  .pot-checker-item::after {
    width: 300%;
    height: 300%;
    border-radius: 0.75rem;
    transform: scale(0.333);
  }
}

.pot-checker-item_active {
  color: #fc9153;
  background: rgba(252,145,83,0.04);
  position: relative;
}

.pot-checker-item_active::after {
  content: "";
  pointer-events: none;
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  transform-origin: 0 0;
  border: 1PX solid #fc9153;
  border-radius: 0.25rem;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
}

@media (min-resolution: 2dppx) {
  .pot-checker-item_active::after {
    width: 200%;
    height: 200%;
    border-radius: 0.5rem;
    transform: scale(0.5);
  }
}

@media (min-resolution: 3dppx) {
  .pot-checker-item_active::after {
    width: 300%;
    height: 300%;
    border-radius: 0.75rem;
    transform: scale(0.333);
  }
}

