.pot-popup {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 100;
  pointer-events: none;
}

.pot-popup_mask {
  pointer-events: auto;
}

.pot-popup_mask .pot-popup-mask {
  display: block;
}

.pot-popup-mask,
.pot-popup-container {
  position: absolute;
  width: 100%;
  height: 100%;
}

.pot-popup-mask {
  display: none;
  overflow: hidden;
  background-color: #25262d;
  opacity: 0.4;
  pointer-events: auto;
}

.pot-popup-mask::before {
  content: ".";
  display: block;
  width: 0.0625rem;
  height: 0.0625rem;
  background-color: rgba(0,0,0,0.1);
  margin-left: -0.625rem;
}

.pot-popup-container {
  transform: translate(100%, 100%);
}

.pot-popup-content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  box-sizing: border-box;
  transform: translate(-100%, -100%);
  pointer-events: auto;
}

.pot-popup-center .pot-popup-content,
.pot-popup-right .pot-popup-content,
.pot-popup-left .pot-popup-content {
  top: -50%;
  left: -50%;
  width: auto;
  max-width: 100%;
  transform: translate(0, 0);
}

.pot-popup-right .pot-popup-content,
.pot-popup-left .pot-popup-content {
  height: 100%;
  top: -100%;
}

.pot-popup-center .pot-popup-content {
  transform: translate(-50%, -50%);
}

.pot-popup-top .pot-popup-content {
  top: -100%;
  left: -100%;
  transform: translate(0, 0);
}

.pot-popup-right .pot-popup-content {
  top: -100%;
  right: 100%;
}

.pot-popup-left .pot-popup-content {
  left: -100%;
}

