.pot-form {
  position: relative;
  font-size: 1rem;
  line-height: 1.429;
  color: #666;
  background-color: #fff;
}

.pot-form_groups .pot-form-group-legend {
  padding: 0.625rem 0.9375rem;
}

.pot-form_groups .pot-form-group-legend:empty {
  padding-top: 0.3125rem;
  padding-bottom: 0.3125rem;
}

.pot-form_standard .pot-form-item {
  min-height: 2.875rem;
}

.pot-form_standard .pot-form-field {
  flex: 1;
  font-size: 0.875rem;
}

.pot-form_standard .pot-validator {
  display: flex;
  align-items: center;
  position: relative;
}

.pot-form_standard .pot-validator_invalid {
  color: #e64340;
}

.pot-form_standard .pot-validator-content {
  flex: 1;
}

.pot-form_standard .pot-validator-msg-def {
  font-size: 0;
}

.pot-form_standard .pot-validator_invalid .pot-validator-msg::before {
  content: "\e614";
  padding-left: 0.3125rem;
  font-family: "pot-icon" !important;
  font-size: 1.25rem;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.0125rem;
  -moz-osx-font-smoothing: grayscale;
}

.pot-form_standard .pot-form-label {
  width: 6.25rem;
  padding-right: 0.625rem;
}

.pot-form_standard .pot-checkbox-group::before,
.pot-form_standard .pot-radio-group::before,
.pot-form_standard .pot-checkbox-group::after,
.pot-form_standard .pot-radio-group::after {
  display: none;
}

.pot-form_standard .pot-input input {
  padding: 0.8125rem 0;
  background-color: transparent;
}

.pot-form_standard .pot-input::after {
  display: none;
}

.pot-form_standard .pot-textarea-wrapper {
  padding: 0.8125rem 0;
  height: 1.25rem;
}

.pot-form_standard .pot-textarea-wrapper.pot-textarea_expanded {
  height: 3.75rem;
  padding-bottom: 1.25rem;
}

.pot-form_standard .pot-textarea-wrapper.pot-textarea_expanded .pot-textarea-indicator {
  bottom: 0.125rem;
}

.pot-form_standard .pot-textarea-wrapper .pot-textarea {
  padding: 0;
  background-color: transparent;
}

.pot-form_standard .pot-textarea-wrapper::after {
  display: none;
}

.pot-form_standard .pot-select {
  padding-left: 0;
  background-color: transparent;
}

.pot-form_standard .pot-select::after {
  display: none;
}

.pot-form_standard .pot-upload-def {
  padding: 0.3125rem 0;
}

.pot-form_standard .pot-upload-def .pot-upload-btn,
.pot-form_standard .pot-upload-def .pot-upload-file {
  margin: 0.3125rem 0.625rem 0.3125rem 0;
}

.pot-form_classic .pot-form-item {
  display: block;
  padding: 0.9375rem;
}

.pot-form_classic .pot-form-item:last-child {
  padding-bottom: 1.875rem;
}

.pot-form_classic .pot-form-item::after {
  display: none;
}

.pot-form_classic .pot-form-item .pot-validator-msg {
  position: absolute;
  margin-top: 0.1875rem;
}

.pot-form_classic .pot-form-item .pot-validator-msg::before {
  display: none;
}

.pot-form_classic .pot-form-item .pot-validator-msg-def {
  font-size: 0.75rem;
}

.pot-form_classic .pot-form-item_btn {
  padding-top: 0;
  padding-bottom: 0;
}

.pot-form_classic .pot-form-item_btn:last-child {
  padding-bottom: 0;
}

.pot-form_classic .pot-form-label {
  padding-bottom: 0.9375rem;
}

.pot-form_fresh .pot-form-item {
  display: block;
  padding: 2em 0.9375rem 0.625rem;
}

.pot-form_fresh .pot-form-item::after {
  display: none;
}

.pot-form_fresh .pot-form-item .pot-validator-msg {
  position: absolute;
  top: 1em;
  right: 0.9375rem;
  bottom: auto;
  margin-top: -0.4em;
  font-size: 0.75rem;
}

.pot-form_fresh .pot-form-item .pot-validator-msg::before {
  display: none;
}

.pot-form_fresh .pot-form-item .pot-validator-msg-def {
  font-size: 100%;
}

.pot-form_fresh .pot-form-item_btn {
  padding-top: 0;
  padding-bottom: 0;
}

.pot-form_fresh .pot-form-item_btn:last-child {
  padding-bottom: 0;
}

.pot-form_fresh .pot-form-label {
  position: absolute;
  top: 1em;
  margin-top: -0.4em;
  font-size: 0.75rem;
}

/*# sourceMappingURL=pot_components/components/form/form.css.map */