@keyframes Myrotate {
  from {
    transform: rotateZ(0deg);
  }

  to {
    transform: rotateZ(360deg);
  }
}

.header {
  background: #fff;
  height: 3.125rem;
  font-size: 1.25rem;
  line-height: 3.125rem;
  /*background: #fff;*/
  position: relative;
  z-index: 10;
  /*box-shadow: 2px 2px 5px rgba(0, 0, 0, .1);*/
}

.header .layui-icon.loading {
  display: inline-block;
  animation: Myrotate 1s ease infinite;
}

.header .right_top.glyphicon {
  font-size: 1.25rem;
  font-weight: 100;
}

.header div {
  float: left;
  color: #000;
}

.header .left {
  line-height: 3.125rem;
  margin-left: 0.625rem;
}

.header .left img {
  width: 1.5rem;
  margin-top: 0.625rem;
}

.header .icon-loading {
  color: #333;
  font-size: 1.25rem;
}

.header .title {
  width: 16.25rem;
  height: 3.125rem;
  line-height: 3.125rem;
  text-align: center;
  left: 0;
  right: 0;
  margin: auto;
  position: absolute;
}

.header .title span {
  vertical-align: middle;
  margin-right: 0.625rem;
}

.header .title img {
  width: 1.375rem;
  margin-bottom: 0.1875rem;
  margin-right: 0.3125rem;
  vertical-align: middle;
}

.header .search {
  position: absolute;
  right: 0.625rem;
  top: 0.9375rem;
}

.header .right_top {
  font-size: 1rem;
  height: 3.125rem;
  width: 3.125rem;
  text-align: center;
  line-height: 3.125rem;
  position: absolute;
  right: 0.625rem;
  color: #2a70d0;
}

.header .right_top i {
  padding: 0;
}

.header .right_top.icon {
  position: absolute;
  top: 0;
  color: #000;
  height: 3.125rem;
  line-height: 3.125rem;
}