.pot-scroll-wrapper {
  position: relative;
  height: 100%;
  overflow: hidden;
}

.pot-scroll-list-wrapper {
  overflow: hidden;
}

.pot-pulldown-wrapper {
  position: absolute;
  width: 100%;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all;
}

.pot-pulldown-wrapper .before-trigger {
  height: 3.375rem;
  line-height: 0;
  padding-top: 0.375rem;
}

.pot-pulldown-wrapper .after-trigger .loading {
  padding: 0.5rem 0;
}

.pot-pulldown-wrapper .after-trigger .pot-pulldown-loaded {
  padding: 0.75rem 0;
}

.pot-pullup-wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.pot-pullup-wrapper .before-trigger {
  padding: 1.375rem 0;
  min-height: 1em;
}

.pot-pullup-wrapper .after-trigger {
  padding: 1.1875rem 0;
}

.pot-scroll-content {
  position: relative;
  z-index: 1;
}

.pot-scroll-item {
  height: 3.75rem;
  line-height: 3.75rem;
  font-size: 1.125rem;
  padding-left: 1.25rem;
}

