.child-view {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1);
}

.slide-disappear-enter {
  opacity: 0;
}

.slide-disappear-leave-active {
  opacity: 0;
}

.slide-left-enter {
  opacity: 0;
  transform: translate(6.25rem, 0);
}

.slide-right-leave-active {
  opacity: 0;
  transform: translate(6.25rem, 0);
}

.slide-right-enter {
  opacity: 0;
  transform: translate(-6.25rem, 0);
}

.slide-left-leave-active {
  opacity: 0;
  transform: translate(-6.25rem, 0);
}

.slide-up-leave-active {
  opacity: 0;
  transform: translate(0, -6.25rem);
}

.slide-up-enter {
  opacity: 0;
  transform: translate(0, 6.25rem);
}

.slide-down-leave-active {
  opacity: 0;
  transform: translate(0, 6.25rem);
}

.slide-down-enter {
  opacity: 0;
  transform: translate(0, -6.25rem);
}