.pot-btn {
  display: block;
  margin: 0;
  padding: 1.0625rem 1rem;
  width: 100%;
  text-align: center;
  white-space: nowrap;
  cursor: pointer;
  font-size: 1rem;
  line-height: 1;
  color: #fff;
  background: #4a4c5b;
  outline: none;
  border: none;
  border-radius: 0.125rem;
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}

.pot-btn.pot-btn_active,
.pot-btn:active {
  background: #444654;
}

.pot-btn > i {
  display: inline-block;
  margin-right: 0.25rem;
  font-size: 100%;
  transform: scale(1.13);
  transform-origin: right center;
}

.pot-btn-inline {
  width: auto;
  display: inline-block;
  vertical-align: middle;
  padding: 0.5625rem 0.625rem;
  font-size: 0.75rem;
}

.pot-btn-inline > i {
  margin-right: 0.125rem;
  transform: scale(1.14);
}

.pot-btn-primary {
  color: #fff;
  background: #fc9153;
}

.pot-btn-primary.pot-btn_active,
.pot-btn-primary:active {
  background: #e8864c;
}

.pot-btn-light {
  color: #666;
  background: #fcfcfc;
  box-shadow: 0 0.0625rem 0.1875rem rgba(0,0,0,0.1);
}

.pot-btn-light.pot-btn_active,
.pot-btn-light:active {
  background: #e8e8e8;
}

.pot-btn-outline {
  color: #666;
  background: transparent;
  position: relative;
}

.pot-btn-outline::after {
  content: "";
  pointer-events: none;
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  transform-origin: 0 0;
  border: 1PX solid #666;
  border-radius: 2PX;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
}

@media (min-resolution: 2dppx) {
  .pot-btn-outline::after {
    width: 200%;
    height: 200%;
    border-radius: 4PX;
    transform: scale(0.5);
  }
}

@media (min-resolution: 3dppx) {
  .pot-btn-outline::after {
    width: 300%;
    height: 300%;
    border-radius: 6PX;
    transform: scale(0.333);
  }
}

.pot-btn-outline.pot-btn_active,
.pot-btn-outline:active {
  background: rgba(0,0,0,0.08);
  position: relative;
}

