.pot-sticky {
  position: relative;
  height: 100%;
  overflow: hidden;
}

.pot-sticky-fixed {
  z-index: 1;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.pot-sticky-fixed-fade-enter,
.pot-sticky-fixed-fade-leave-active {
  opacity: 0;
}

.pot-sticky-fixed-fade-enter-active,
.pot-sticky-fixed-fade-leave-active {
  transition: all 0.2s ease-in-out;
}

