.pot-picker-panel .checkbox {
  width: 1.25rem;
  height: 1.25rem;
  border: 0.0625rem solid #ddd;
  display: inline-block;
  border-radius: 0.1875rem;
}

.pot-picker-panel.isCheckBox .pot-picker-wheel-item {
  transform: none;
}

.pot-picker-fade-enter,
.pot-picker-fade-leave-active {
  opacity: 0;
}

.pot-picker-fade-enter-active,
.pot-picker-fade-leave-active {
  transition: all 0.3s ease-in-out;
}

.pot-picker-panel {
  height: 17.0625rem;
  text-align: center;
  font-size: 0.875rem;
  background: #fff;
}

.pot-picker-move-enter,
.pot-picker-move-leave-active {
  transform: translate3d(0, 100%, 0);
}

.pot-picker-move-enter-active,
.pot-picker-move-leave-active {
  transition: all 0.3s ease-in-out;
}

.pot-picker-choose {
  position: relative;
  height: 3.75rem;
}

.pot-picker-confirm,
.pot-picker-cancel {
  font-size: 0.875rem;
  line-height: 3.75rem;
  padding: 0 1rem;
  box-sizing: content-box;
  font-size: 0.875rem;
}

.pot-picker-confirm {
  position: absolute;
  right: 0;
  color: #fc9153;
}

.pot-picker-confirm:active {
  color: #fdc2a5;
}

.pot-picker-cancel {
  position: absolute;
  left: 0;
  color: #999;
}

.pot-picker-cancel:active {
  color: #ccc;
}

.pot-picker-title-group {
  padding: 0 3.75rem;
  display: flex;
  height: 100%;
  flex-flow: column;
  justify-content: center;
  text-align: center;
}

.pot-picker-title {
  font-size: 1.125rem;
  line-height: 1.5625rem;
  font-weight: normal;
  color: #333;
}

.pot-picker-subtitle {
  margin-top: 0.125rem;
  line-height: 1rem;
  font-size: 0.75rem;
  color: #999;
}

.pot-picker-content {
  position: relative;
  top: 1.25rem;
}

.pot-picker-content > i {
  position: absolute;
  z-index: 10;
  left: 0;
  width: 100%;
  height: 4.25rem;
  pointer-events: none;
  transform: translateZ(0);
}

.pot-picker-content > .border-bottom-1px {
  top: 0;
  background: linear-gradient(to top, rgba(255,255,255,0.4), rgba(255,255,255,0.8));
}

.pot-picker-content > .border-top-1px {
  bottom: 0;
  background: linear-gradient(to bottom, rgba(255,255,255,0.4), rgba(255,255,255,0.8));
}

.pot-picker-wheel-wrapper {
  display: flex;
  padding: 0 1rem;
}

.pot-picker-wheel-wrapper > div {
  flex: 1;
  flex-basis: 1e-0.5625rem;
  width: 1%;
  height: 10.8125rem;
  overflow: hidden;
  font-size: 1.25rem;
}

.pot-picker-wheel-scroll {
  padding: 0;
  margin-top: 4.25rem;
  line-height: 2.25rem;
  list-style: none;
}

.pot-picker-wheel-item {
  list-style: none;
  height: 2.25rem;
  overflow: hidden;
  white-space: nowrap;
  color: #333;
}

.pot-picker-footer {
  height: 1.25rem;
}

