.pot-tip {
  display: flex;
  z-index: 10;
  position: absolute;
  padding: 0.625rem 2.375rem 0.625rem 1rem;
  max-height: 3.75rem;
  font-size: 0.75rem;
  color: #fff;
  background: rgba(74,76,91,0.8);
  border-radius: 0.125rem;
  transition: opacity 0.2s;
}

.pot-tip[data-dir="top"] .pot-tip-angle,
.pot-tip[data-dir="bottom"] .pot-tip-angle {
  left: 50%;
  transform: translateX(-50%);
}

.pot-tip[data-dir="top"] .pot-tip-angle {
  top: 0;
}

.pot-tip[data-dir="top"] .pot-tip-angle::before {
  margin-top: -0.375rem;
  transform: rotate(0deg);
}

.pot-tip[data-dir="bottom"] .pot-tip-angle {
  bottom: 0;
}

.pot-tip[data-dir="bottom"] .pot-tip-angle::before {
  margin-bottom: -0.375rem;
  transform: rotate(180deg);
}

.pot-tip[data-dir="left"] .pot-tip-angle,
.pot-tip[data-dir="right"] .pot-tip-angle {
  top: 50%;
  transform: translateY(-50%);
}

.pot-tip[data-dir="left"] .pot-tip-angle {
  left: 0;
}

.pot-tip[data-dir="left"] .pot-tip-angle::before {
  margin-left: -0.5625rem;
  transform: rotate(-90deg);
}

.pot-tip[data-dir="right"] .pot-tip-angle {
  right: 0;
}

.pot-tip[data-dir="right"] .pot-tip-angle::before {
  margin-right: -0.5625rem;
  transform: rotate(90deg);
}

.pot-tip-angle {
  position: absolute;
}

.pot-tip-angle::before {
  content: "";
  display: block;
  border-width: 0 0.375rem 0.375rem;
  border-style: solid;
  border-color: transparent transparent rgba(74,76,91,0.8);
}

.pot-tip-close {
  position: absolute;
  right: 0.875rem;
  top: 0.75rem;
  width: 0.75rem;
  height: 0.75rem;
  padding: 0;
  color: inherit;
  outline: none;
  border: none;
  background: none;
  transform: scale(1.3);
}

.pot-tip-content {
  min-height: 1.125rem;
  line-height: 1.125rem;
  flex: 1;
  flex-basis: 1e-0.5625rem;
  width: 1%;
  overflow: hidden;
}

.pot-tip-zoom-enter-active {
  animation: tip-in 0.4s;
}

.pot-tip-zoom-leave-active {
  animation: tip-out 0.2s;
}

@-moz-keyframes tip-in {
  0% {
    transform: scale(0);
  }

  50% {
    transform: scale(1.1);
  }

  100% {
    transform: scale(1);
  }
}

@-webkit-keyframes tip-in {
  0% {
    transform: scale(0);
  }

  50% {
    transform: scale(1.1);
  }

  100% {
    transform: scale(1);
  }
}

@-o-keyframes tip-in {
  0% {
    transform: scale(0);
  }

  50% {
    transform: scale(1.1);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes tip-in {
  0% {
    transform: scale(0);
  }

  50% {
    transform: scale(1.1);
  }

  100% {
    transform: scale(1);
  }
}

@-moz-keyframes tip-out {
  from {
    transform: scale(1);
    opacity: 1;
  }

  to {
    transform: scale(0);
    opacity: 0;
  }
}

@-webkit-keyframes tip-out {
  from {
    transform: scale(1);
    opacity: 1;
  }

  to {
    transform: scale(0);
    opacity: 0;
  }
}

@-o-keyframes tip-out {
  from {
    transform: scale(1);
    opacity: 1;
  }

  to {
    transform: scale(0);
    opacity: 0;
  }
}

@keyframes tip-out {
  from {
    transform: scale(1);
    opacity: 1;
  }

  to {
    transform: scale(0);
    opacity: 0;
  }
}

