.pot-slide {
  position: relative;
  min-height: 0.0625rem;
  height: 100%;
  overflow: hidden;
}

.pot-slide-group {
  position: relative;
  height: 100%;
  overflow: hidden;
  white-space: nowrap;
}

.pot-slide-dots {
  position: absolute;
  bottom: 0.125rem;
  right: 0;
  left: 0;
  padding: 0 0.375rem;
  font-size: 0;
  text-align: center;
  transform: translateZ(0.0625rem);
}

.pot-slide-dots > span {
  display: inline-block;
  vertical-align: bottom;
  margin: 0 0.0625rem;
  width: 0.625rem;
  height: 0.0625rem;
  background: #ccc;
}

.pot-slide-dots > span.active {
  background: #fc9153;
}

