.pot-select {
  position: relative;
  box-sizing: border-box;
  padding: 0.625rem 1.25rem 0.625rem 0.625rem;
  border-radius: 0.125rem;
  font-size: 0.875rem;
  line-height: 1.429;
  color: #666;
  background-color: #fff;
  position: relative;
}

.pot-select::after {
  content: "";
  pointer-events: none;
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  transform-origin: 0 0;
  border: 1PX solid #ccc;
  border-radius: 0.125rem;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
}

@media (min-resolution: 2dppx) {
  .pot-select::after {
    width: 200%;
    height: 200%;
    border-radius: 0.25rem;
    transform: scale(0.5);
  }
}

@media (min-resolution: 3dppx) {
  .pot-select::after {
    width: 300%;
    height: 300%;
    border-radius: 0.375rem;
    transform: scale(0.333);
  }
}

.pot-select > span {
  display: inline-block;
}

.pot-select_active {
  position: relative;
}

.pot-select_active::after {
  content: "";
  pointer-events: none;
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  transform-origin: 0 0;
  border: 1PX solid #fc9153;
  border-radius: 2PX;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
}

@media (min-resolution: 2dppx) {
  .pot-select_active::after {
    width: 200%;
    height: 200%;
    border-radius: 4PX;
    transform: scale(0.5);
  }
}

@media (min-resolution: 3dppx) {
  .pot-select_active::after {
    width: 300%;
    height: 300%;
    border-radius: 6PX;
    transform: scale(0.333);
  }
}

.pot-select_active .pot-select-icon {
  transform: translate(0, -50%) rotate(180deg);
}

.pot-select_disabled {
  color: #b8b8b8;
  background-color: rgba(0,0,0,0.04);
  cursor: not-allowed;
}

.pot-select-placeholder {
  color: #ccc;
}

.pot-select-icon {
  position: absolute;
  right: 0.5rem;
  top: 50%;
  transform: translate(0, -50%);
  border-style: solid;
  border-color: #999 transparent transparent transparent;
  border-width: 0.25rem 0.25rem 0 0.25rem;
  transition: transform 0.3s ease-in-out;
}

