.pot-swipe-item {
  position: relative;
}

.pot-swipe-item-inner {
  height: 3.75rem;
  line-height: 3.75rem;
  font-size: 1rem;
  padding-left: 1.25rem;
}

.pot-swipe-btn {
  display: flex;
  align-items: center;
  position: absolute;
  top: 0;
  left: 100%;
  height: 100%;
  text-align: left;
  font-size: 1rem;
}

.pot-swipe-btn .text {
  flex: 1;
  padding: 0 1.25rem;
  white-space: nowrap;
  color: #fff;
}

