.pot-radio {
  position: relative;
  padding: 0 1rem;
  text-align: left;
  font-size: 100%;
  color: #666;
}

.pot-radio[data-pos="right"] .pot-radio-ui {
  margin-right: 0;
  position: absolute;
  right: 0;
}

.pot-radio[data-pos="right"] .pot-radio-label {
  margin-right: 1.42em;
}

.pot-radio-wrap {
  position: relative;
  display: flex;
  align-items: center;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding: 0.6875rem 0;
  line-height: 1.5;
  word-break: break-word;
  word-wrap: break-word;
}

.pot-radio-input {
  z-index: 1;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}

.pot-radio-ui {
  position: relative;
  width: 1em;
  height: 1em;
  margin-right: 0.42em;
  line-height: 1;
  color: transparent;
  background-color: transparent;
  border-radius: 50%;
}

.pot-radio-ui::before,
.pot-radio-ui i {
  transition: all 0.2s;
}

.pot-radio-ui::before {
  color: #ccc;
  display: inline-block;
  transform: scale(1.24);
}

.pot-radio-ui i {
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  transform: scale(0.4);
}

.pot-radio-ui i::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 50%;
  height: 50%;
  transform: translate(-50%, -50%) scale(0.8);
  border-radius: 50%;
}

.pot-radio_selected .pot-radio-ui {
  background-color: #fc9153;
}

.pot-radio_selected .pot-radio-ui::before {
  color: transparent;
}

.pot-radio_selected .pot-radio-ui i {
  transform: scale(1);
}

.pot-radio_selected .pot-radio-ui i::before {
  background-color: #fff;
}

.pot-radio_disabled .pot-radio-ui {
  background-color: #eee;
}

.pot-radio_disabled .pot-radio-ui::before,
.pot-radio_disabled .pot-radio-ui i {
  transition: none;
}

.pot-radio_disabled .pot-radio-ui::before {
  color: transparent;
}

.pot-radio-hollow.pot-radio_selected .pot-radio-ui,
.pot-radio-hollow.pot-radio_disabled .pot-radio-ui {
  background-color: transparent;
}

.pot-radio-hollow.pot-radio_selected .pot-radio-ui i::before,
.pot-radio-hollow.pot-radio_disabled .pot-radio-ui i::before {
  transform: translate(-50%, -50%) scale(1);
}

.pot-radio-hollow.pot-radio_selected .pot-radio-ui::before {
  color: #fc9153;
}

.pot-radio-hollow.pot-radio_selected .pot-radio-ui i::before {
  background-color: #fc9153;
}

.pot-radio-hollow.pot-radio_disabled .pot-radio-ui::before {
  color: #eee;
}

.pot-radio-hollow.pot-radio_disabled.pot-radio_selected .pot-radio-ui i::before {
  background-color: #eee;
}

