.pot-input {
  display: flex;
  align-items: center;
  font-size: 0.875rem;
  line-height: 1.429;
  background-color: #fff;
  position: relative;
}

.pot-input::after {
  content: "";
  pointer-events: none;
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  transform-origin: 0 0;
  border: 1PX solid #ebebeb;
  border-radius: 2PX;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
}

@media (min-resolution: 2dppx) {
  .pot-input::after {
    width: 200%;
    height: 200%;
    border-radius: 4PX;
    transform: scale(0.5);
  }
}

@media (min-resolution: 3dppx) {
  .pot-input::after {
    width: 300%;
    height: 300%;
    border-radius: 6PX;
    transform: scale(0.333);
  }
}

.pot-input-field {
  display: block;
  flex: 1;
  width: 100%;
  padding: 0.625rem;
  box-sizing: border-box;
  color: #666;
  line-height: inherit;
  background-color: inherit;
  border-radius: 0.125rem;
  outline: none;
}

.pot-input-field::-webkit-input-placeholder {
  color: #ccc !important;
  text-overflow: ellipsis;
}

.pot-input-field + .pot-input-append .pot-input-clear:first-child,
.pot-input-field + .pot-input-append .pot-input-eye:first-child {
  margin-left: -0.3125rem;
}

.pot-input_active::after {
  border-color: #fc9153;
}

.pot-input-prepend,
.pot-input-append {
  display: flex;
  align-items: center;
}

.pot-input-clear,
.pot-input-eye {
  width: 1em;
  height: 1em;
  line-height: 1;
  padding: 0.625rem;
  box-sizing: content-box;
  color: #999;
}

.pot-input-clear > i,
.pot-input-eye > i {
  display: inline-block;
  transform: scale(1.2);
}

.pot-input-eye > .potic-eye-invisible,
.pot-input-eye > .potic-eye-visible {
  transform: scale(1.4);
}

