.pot-form-item {
  position: relative;
  display: flex;
  align-items: center;
  padding: 0 0.9375rem;
}

.pot-form-item:last-child::after {
  display: none;
}

.pot-form-item .pot-checkbox-group,
.pot-form-item .pot-radio-group {
  background-color: transparent;
}

.pot-form-item .pot-checkbox,
.pot-form-item .pot-radio {
  padding-left: 0;
  padding-right: 0;
}

.pot-form-item_btn {
  margin: 0.9375rem 0;
}

.pot-form-item_btn::after {
  display: none;
}

.pot-form-label {
  display: flex;
  align-items: center;
  word-wrap: break-word;
  word-break: break-word;
}

.pot-form-item_required .pot-form-label::before {
  content: "*";
  display: block;
  margin-top: 0.0625rem;
  margin-right: 0.3em;
  color: #e64340;
}

