.pot-switch {
  display: flex;
  position: relative;
  align-items: center;
}

.pot-switch .pot-switch-input {
  position: absolute;
  z-index: 1;
  width: 3rem;
  height: 1.75rem;
  opacity: 0;
}

.pot-switch .pot-switch-input:checked + .pot-switch-ui {
  border-color: #fc9153;
  background-color: #fc9153;
}

.pot-switch .pot-switch-input:checked + .pot-switch-ui::before {
  transform: scale(0);
}

.pot-switch .pot-switch-input:checked + .pot-switch-ui::after {
  transform: translateX(1.25rem);
}

.pot-switch .pot-switch-input:disabled + .pot-switch-ui {
  opacity: 0.3;
}

.pot-switch .pot-switch-ui {
  position: relative;
  display: block;
  width: 3rem;
  height: 1.75rem;
  box-sizing: content-box;
  border: 0.0625rem solid #e4e4e4;
  border-radius: 1.75rem;
  background-color: #e4e4e4;
}

.pot-switch .pot-switch-ui::before,
.pot-switch .pot-switch-ui::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 1.75rem;
  background-color: #fff;
  transition: transform 0.4s cubic-bezier(0.25, 0.1, 0.25, 1.28);
}

.pot-switch .pot-switch-ui::after {
  width: 1.75rem;
  background-color: #fff;
  box-shadow: 0 0.0625rem 0.1875rem rgba(0,0,0,0.4);
}

.pot-switch .pot-switch-label {
  display: block;
  margin-left: 0.625rem;
}

.pot-switch .pot-switch-label:empty {
  margin-left: 0;
}

