.pot-upload-btn {
  position: relative;
  overflow: hidden;
}

.pot-upload-btn:active .pot-upload-btn-def {
  background-color: rgba(0,0,0,0.04);
}

.pot-upload-input {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  font-size: 0;
  opacity: 0;
}

.pot-upload-btn-def {
  position: relative;
  width: 5rem;
  height: 5rem;
  box-sizing: border-box;
  background-color: #fff;
  box-shadow: 0 0 0.375rem 0.125rem rgba(0,0,0,0.08);
  border-radius: 0.125rem;
  position: relative;
}

.pot-upload-btn-def::after {
  content: "";
  pointer-events: none;
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  transform-origin: 0 0;
  border: 1PX solid #e5e5e5;
  border-radius: 0.125rem;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
}

@media (min-resolution: 2dppx) {
  .pot-upload-btn-def::after {
    width: 200%;
    height: 200%;
    border-radius: 0.25rem;
    transform: scale(0.5);
  }
}

@media (min-resolution: 3dppx) {
  .pot-upload-btn-def::after {
    width: 300%;
    height: 300%;
    border-radius: 0.375rem;
    transform: scale(0.333);
  }
}

.pot-upload-btn-def > i::before,
.pot-upload-btn-def > i::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 1.25rem;
  height: 0.125rem;
  transform: translate(-50%, -50%);
  background-color: #666;
}

.pot-upload-btn-def > i::after {
  transform: translate(-50%, -50%) rotate(90deg);
}

