.pot-drawer {
  position: absolute;
  z-index: 5;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
  color: #333;
}

.pot-drawer-main {
  position: absolute;
  top: 0;
  left: 100%;
  bottom: 0;
  max-width: 90%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  transform: translate3d(0, 0, 0);
  transition: transform 0.3s ease-in-out;
  box-shadow: -0.125rem 0 0.125rem rgba(0,0,0,0.2);
}

.pot-drawer-title {
  position: relative;
  padding: 0 1.25rem;
  height: 3.125rem;
  line-height: 3.125rem;
  border-bottom: 0.0625rem solid #eee;
  font-size: 1rem;
  background-color: #fff;
}

.pot-drawer-panels {
  display: flex;
  flex: 1;
}

