.select_list {
  margin-top: 0.625rem;
}

.select_item {
  position: relative;
  height: 1.875rem;
  text-align: left;
  line-height: 1.25rem;
  padding-top: 0.3125rem;
}

.select_item.active .icon {
  background: #00f;
}

.select_item.active .icon svg {
  display: block;
}

.select_item .text {
  vertical-align: middle;
  display: inline-block;
  margin-left: 1.875rem;
  line-height: 1.25rem;
  height: 1.25rem;
}

.select_item .icon {
  vertical-align: middle;
  line-height: 1.25rem;
  border: 0.0625rem solid #ddd;
  border-radius: 50%;
  margin-left: 1.875rem;
  display: inline-block;
  width: 1.25rem;
  height: 1.25rem;
  left: 1.25rem;
}

.select_item .icon svg {
  line-height: 1.25rem;
  display: inline-block;
  display: none;
  animation: pot_mymove 0.3s;
  width: 0.8125rem;
  height: 0.8125rem;
  margin-left: 0.1875rem;
  margin-top: 0.1875rem;
}

@-moz-keyframes pot_mymove {
  from {
    transform: scale(0, 0);
  }

  to {
    transform: scale(1, 1);
  }
}

@-webkit-keyframes pot_mymove {
  from {
    transform: scale(0, 0);
  }

  to {
    transform: scale(1, 1);
  }
}

@-o-keyframes pot_mymove {
  from {
    transform: scale(0, 0);
  }

  to {
    transform: scale(1, 1);
  }
}

@keyframes pot_mymove {
  from {
    transform: scale(0, 0);
  }

  to {
    transform: scale(1, 1);
  }
}

.pot-dialog-main {
  width: 16.875rem;
  padding: 0;
  text-align: center;
  overflow: hidden;
  border-radius: 0.125rem;
  background-color: #fff;
}

.pot-dialog-confirm,
.pot-dialog-alert {
  position: relative;
  overflow: hidden;
}

.pot-dialog-icon {
  margin-top: 1.25rem;
  margin-bottom: 1rem;
  line-height: 1;
  color: #4a4c5b;
  font-size: 1.875rem;
}

.pot-dialog-icon i {
  display: inline-block;
  width: 1.875rem;
  height: 1.875rem;
  padding: 0.625rem;
  box-sizing: content-box;
  border-radius: 50%;
  background-color: #f3f4f5;
}

.pot-dialog-icon + .pot-dialog-title .pot-dialog-title-def {
  margin-top: 0;
}

.pot-dialog-icon + .pot-dialog-content {
  margin-top: -0.25rem;
}

.pot-dialog-title {
  color: #333;
  font-size: 1rem;
  line-height: 1;
}

.pot-dialog-title + .pot-dialog-content {
  margin-top: 0.75rem;
}

.pot-dialog-title-def {
  margin: 1.875rem 1rem 0;
  overflow: hidden;
  white-space: nowrap;
}

.pot-dialog-content {
  margin: 1rem 0;
  text-align: left;
  color: #666;
  font-size: 0.875rem;
  line-height: 1.375rem;
}

.pot-dialog-content-def {
  padding: 0 1rem;
}

.pot-dialog-content-def > p {
  display: table;
  margin: auto;
}

.pot-dialog-content-def > p + .pot-input {
  margin-top: 0.75rem;
}

.pot-dialog-confirm .pot-dialog-btns .pot-dialog-btn,
.pot-dialog-prompt .pot-dialog-btns .pot-dialog-btn {
  width: 50%;
  float: left;
}

.pot-dialog-confirm .pot-dialog-btns.border-right-1px::after,
.pot-dialog-prompt .pot-dialog-btns.border-right-1px::after {
  right: 50%;
  border-color: #ebebeb;
}

.pot-dialog-close {
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1;
  position: absolute;
  top: 0;
  right: 0;
  width: 2rem;
  height: 2rem;
  color: #999;
  font-size: 1.125rem;
}

.pot-dialog-btns {
  overflow: hidden;
  width: 100%;
  font-size: 0;
}

.pot-dialog-btn {
  display: inline-block;
  width: 100%;
  padding: 1.0625rem 0.625rem;
  margin: 0;
  font-size: 1rem;
  line-height: 1;
  text-align: center;
  text-decoration: none;
  color: #999;
  background-color: #fff;
  background-clip: padding-box;
  box-sizing: border-box;
}

.pot-dialog-btn:active {
  background-color: rgba(0,0,0,0.04);
}

.pot-dialog-btn_highlight {
  color: #fc9153;
}

.pot-dialog-btn_highlight:active {
  background-color: rgba(252,145,83,0.04);
}

.pot-dialog-btn_disabled {
  color: #999;
}

.pot-dialog-btn_disabled:active {
  background-color: transparent;
}

.pot-dialog-fade-enter-active {
  animation: dialog-fadein 0.4s;
}

.pot-dialog-fade-enter-active .pot-dialog-main {
  animation: dialog-zoom 0.4s;
}

@-moz-keyframes dialog-fadein {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@-webkit-keyframes dialog-fadein {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@-o-keyframes dialog-fadein {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes dialog-fadein {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@-moz-keyframes dialog-zoom {
  0% {
    transform: scale(0);
  }

  50% {
    transform: scale(1.1);
  }

  100% {
    transform: scale(1);
  }
}

@-webkit-keyframes dialog-zoom {
  0% {
    transform: scale(0);
  }

  50% {
    transform: scale(1.1);
  }

  100% {
    transform: scale(1);
  }
}

@-o-keyframes dialog-zoom {
  0% {
    transform: scale(0);
  }

  50% {
    transform: scale(1.1);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes dialog-zoom {
  0% {
    transform: scale(0);
  }

  50% {
    transform: scale(1.1);
  }

  100% {
    transform: scale(1);
  }
}

