.pot-toolbar-item {
  display: flex;
  flex: 1;
  flex-basis: 1e-0.5625rem;
  width: 1%;
  align-items: center;
  background-color: #fcfcfc;
  /* reset pot-checkbox style */
  /* reset pot-btn style */
}

.pot-toolbar-item:last-child {
  position: static;
}

.pot-toolbar-item:last-child::after {
  display: none;
}

.pot-toolbar-item:active {
  background-color: #e8e8e8;
}

.pot-toolbar-item:active::after {
  border-color: transparent;
}

.pot-toolbar-item .pot-toolbar-chb {
  height: 2.75rem;
  padding: 0;
  font-size: 0.75rem;
  background-color: transparent;
}

.pot-toolbar-item .pot-toolbar-chb .pot-checkbox-wrap {
  padding: 0;
  justify-content: center;
}

.pot-toolbar-item .pot-toolbar-chb .pot-checkbox-wrap .pot-checkbox-ui {
  position: relative;
  left: auto;
  margin-right: 0.42em;
}

.pot-toolbar-item .pot-btn {
  position: relative;
  padding: 0 0.3125rem;
  background-color: transparent;
  border: 0 none;
  color: #666;
  font-size: 0.75rem;
}

.pot-toolbar-item .pot-btn:active {
  background-color: transparent;
}

.pot-toolbar-item .pot-btn:active::after {
  display: none;
}

.pot-toolbar-item .pot-btn i {
  margin-right: 0;
}

.pot-toolbar-item .pot-btn i.potic-more {
  color: #999;
  font-size: 1rem;
}

