.pot-checkbox {
  position: relative;
  padding: 0 1rem;
  text-align: left;
  font-size: 100%;
  color: #666;
}

.pot-checkbox[data-pos="right"] .pot-checkbox-ui {
  margin-right: 0;
  position: absolute;
  right: 0;
}

.pot-checkbox[data-pos="right"] .pot-checkbox-label {
  margin-right: 1.42em;
}

.pot-checkbox-wrap {
  position: relative;
  display: flex;
  align-items: center;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding: 0.6875rem 0;
  line-height: 1.5;
  word-break: break-word;
  word-wrap: break-word;
}

.pot-checkbox-input {
  z-index: 1;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}

.pot-checkbox-ui {
  position: relative;
  width: 1em;
  height: 1em;
  margin-right: 0.42em;
  line-height: 1;
  border-radius: 50%;
}

.pot-checkbox-ui.potic-square-border {
  border-radius: 0.125rem;
}

.pot-checkbox-ui::before,
.pot-checkbox-ui i {
  transition: all 0.2s;
}

.pot-checkbox-ui::before {
  color: #ccc;
  display: inline-block;
  transform: scale(1.24);
}

.pot-checkbox-ui i {
  position: absolute;
  top: 0;
  left: 0;
  color: transparent;
  transform: scale(0.4);
}

.pot-checkbox_checked .pot-checkbox-ui::before {
  color: transparent;
}

.pot-checkbox_checked .pot-checkbox-ui i {
  color: #fc9153;
  transform: scale(1.23);
}

.pot-checkbox_disabled .pot-checkbox-ui {
  background-color: #eee;
}

.pot-checkbox_disabled .pot-checkbox-ui::before,
.pot-checkbox_disabled .pot-checkbox-ui i {
  transition: none;
}

.pot-checkbox_disabled .pot-checkbox-ui::before {
  color: transparent;
}

.pot-checkbox_disabled .pot-checkbox-ui i {
  color: #eee;
}

.pot-checkbox_checked.pot-checkbox_disabled .pot-checkbox-ui {
  background-color: #fff;
}

.pot-checkbox-hollow i {
  width: 100%;
  height: 100%;
}

.pot-checkbox-hollow i::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 50%;
  height: 50%;
  transform: translate(-50%, -50%);
  background-color: currentColor;
  border-radius: 0.125rem;
}

.pot-checkbox-hollow.pot-checkbox_checked .pot-checkbox-ui::before {
  color: #fc9153;
}

.pot-checkbox-hollow.pot-checkbox_checked .pot-checkbox-ui i {
  transform: scale(1);
  color: #fc9153;
}

.pot-checkbox-hollow.pot-checkbox_disabled .pot-checkbox-ui {
  background-color: transparent;
}

.pot-checkbox-hollow.pot-checkbox_disabled .pot-checkbox-ui::before {
  color: #eee;
}

.pot-checkbox-hollow.pot-checkbox_disabled .pot-checkbox-ui i {
  transform: scale(1);
  color: transparent;
}

.pot-checkbox-hollow.pot-checkbox_disabled.pot-checkbox_checked .pot-checkbox-ui i {
  color: #eee;
}

