.mh-cell {
  background: #fff;
  margin: 0.3125rem;
  border-radius: 0.3125rem;
  padding: 0.625rem;
  display: flex;
}

.mh-cell .image {
  width: 5rem;
  height: 5rem;
  background-size: cover;
  border-radius: 2.5rem;
}

.mh-cell .content {
  padding-left: 1.25rem;
}

.mh-cell .content .title {
  height: 1.875rem;
  line-height: 1.875rem;
}

.mh-cell .content .middle {
  height: 1.875rem;
  line-height: 1.875rem;
}

.mh-cell .content .bottom {
  height: 1.875rem;
  line-height: 1.875rem;
}