.pot-toast.pot-popup {
  z-index: 900;
}

.pot-toast .pot-popup-content {
  display: flex;
  align-items: center;
  padding: 0.8125rem 1rem;
  color: #ccc;
  background-color: rgba(37,38,45,0.9);
  border-radius: 0.125rem;
}

.pot-toast-icon {
  width: 1.5rem;
  height: 1.5rem;
  font-size: 1.5rem;
}

.pot-toast-tip {
  line-height: 1.25rem;
  font-size: 0.875rem;
  max-width: 12em;
  max-height: 2.5rem;
  overflow: hidden;
}

.pot-toast-icon ~ .pot-toast-tip,
.pot-loading ~ .pot-toast-tip {
  margin-left: 0.5rem;
}

.pot-toast-fade-enter-active {
  animation: toast-in 0.2s;
}

.pot-toast-fade-leave-active {
  animation: toast-out 0.2s;
}

@-moz-keyframes toast-in {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@-webkit-keyframes toast-in {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@-o-keyframes toast-in {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes toast-in {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@-moz-keyframes toast-out {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

@-webkit-keyframes toast-out {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

@-o-keyframes toast-out {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

@keyframes toast-out {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

