.pot-textarea-wrapper {
  position: relative;
  transition: height 200ms;
  height: 2.5rem;
  font-size: 0.875rem;
  line-height: 1.429;
  position: relative;
}

.pot-textarea-wrapper textarea::-webkit-input-placeholder {
  color: #ccc !important;
  text-overflow: ellipsis;
}

.pot-textarea-wrapper::after {
  content: "";
  pointer-events: none;
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  transform-origin: 0 0;
  border: 1PX solid #ebebeb;
  border-radius: 2PX;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
}

@media (min-resolution: 2dppx) {
  .pot-textarea-wrapper::after {
    width: 200%;
    height: 200%;
    border-radius: 4PX;
    transform: scale(0.5);
  }
}

@media (min-resolution: 3dppx) {
  .pot-textarea-wrapper::after {
    width: 300%;
    height: 300%;
    border-radius: 6PX;
    transform: scale(0.333);
  }
}

.pot-textarea_expanded {
  height: 5rem;
}

.pot-textarea_active {
  position: relative;
}

.pot-textarea_active::after {
  content: "";
  pointer-events: none;
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  transform-origin: 0 0;
  border: 1PX solid #fc9153;
  border-radius: 2PX;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
}

@media (min-resolution: 2dppx) {
  .pot-textarea_active::after {
    width: 200%;
    height: 200%;
    border-radius: 4PX;
    transform: scale(0.5);
  }
}

@media (min-resolution: 3dppx) {
  .pot-textarea_active::after {
    width: 300%;
    height: 300%;
    border-radius: 6PX;
    transform: scale(0.333);
  }
}

.pot-textarea-indicator {
  position: absolute;
  bottom: 0.4375rem;
  right: 0.625rem;
  color: #ccc;
}

.pot-textarea {
  width: 100%;
  height: 100%;
  text-align: left;
  padding: 0.625rem;
  box-sizing: border-box;
  font-size: 100%;
  line-height: inherit;
  color: #666;
  background-color: #fff;
  border-radius: 0.125rem;
  resize: none;
  border: none;
  outline: none;
}

