.pot-toolbar {
  position: fixed;
  left: 0.625rem;
  right: 0.625rem;
  bottom: 0.625rem;
  z-index: 2;
  padding-bottom: constant(safe-area-inset-bottom);
  padding-bottom: env(safe-area-inset-bottom);
}

.pot-toolbar-group {
  display: flex;
  height: 2.75rem;
  overflow: hidden;
  box-sizing: border-box;
  border-radius: 0.125rem;
  box-shadow: 0 0.0625rem 0.375rem rgba(0,0,0,0.24);
  background-color: #fcfcfc;
}

.pot-toolbar-group-more {
  margin-bottom: 0.625rem;
}

.pot-toolbar-group-more .pot-toolbar-item:last-child .pot-toolbar-down {
  position: absolute;
  top: 2.75rem;
  right: 9%;
  height: 0.625rem;
  color: #fcfcfc;
  font-size: 1.5rem;
  font-family: pot-icon;
  font-style: normal;
  text-shadow: 0 0.0625rem 0.1875rem #e8e8e8;
  transform: scale(1.3);
}

.pot-toolbar-group-more .pot-toolbar-item:last-child .pot-toolbar-down::before {
  content: "\E603";
  position: relative;
  top: -0.625rem;
}

.pot-toolbar-group-more .pot-toolbar-item:last-child .pot-toolbar-down::after {
  content: "";
  display: block;
  position: absolute;
  left: 30%;
  top: 50%;
  margin-top: -0.375rem;
  width: 40%;
  height: 0.125rem;
  background-color: #fcfcfc;
}

.pot-toolbar-group-more .pot-toolbar-item:last-child:active .pot-toolbar-down {
  color: #e8e8e8;
}

.pot-toolbar-group-more .pot-toolbar-item:last-child:active .pot-toolbar-down::after {
  background-color: #e8e8e8;
}

