.pot-radio-group {
  z-index: 1;
  overflow: hidden;
  background-color: #fff;
}

.pot-radio-group .pot-radio:last-child .pot-radio-wrap::before {
  display: none;
}

.pot-radio-group .pot-radio:last-child .pot-radio-wrap::after {
  display: none;
}

.pot-radio-group[data-horz="true"] {
  display: flex;
  padding-left: 0;
  position: relative;
  border-radius: 0.125rem;
}

.pot-radio-group[data-horz="true"]::after {
  content: "";
  pointer-events: none;
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  transform-origin: 0 0;
  border: 1PX solid #ccc;
  border-radius: 0.125rem;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
}

@media (min-resolution: 2dppx) {
  .pot-radio-group[data-horz="true"]::after {
    width: 200%;
    height: 200%;
    border-radius: 0.25rem;
    transform: scale(0.5);
  }
}

@media (min-resolution: 3dppx) {
  .pot-radio-group[data-horz="true"]::after {
    width: 300%;
    height: 300%;
    border-radius: 0.375rem;
    transform: scale(0.333);
  }
}

.pot-radio-group[data-horz="true"] .pot-radio {
  flex: 1;
  flex-basis: 1e-0.5625rem;
  width: 1%;
  text-align: center;
  padding-left: 0.625rem;
  padding-right: 0.625rem;
}

.pot-radio-group[data-horz="true"] .pot-radio:after {
  border-color: #ccc;
}

.pot-radio-group[data-horz="true"] .pot-radio:last-child::before {
  display: none;
}

.pot-radio-group[data-horz="true"] .pot-radio:last-child::after {
  display: none;
}

.pot-radio-group[data-horz="true"] .pot-radio:last-child::before {
  display: none;
}

.pot-radio-group[data-horz="true"] .pot-radio:last-child::after {
  display: none;
}

.pot-radio-group[data-horz="true"] .pot-radio[data-pos="right"] .pot-radio-ui {
  position: relative;
  margin-left: 0.42em;
  order: 1;
}

.pot-radio-group[data-horz="true"] .pot-radio[data-pos="right"] .pot-radio-label {
  margin-right: 0;
}

.pot-radio-group[data-horz="true"] .pot-radio-wrap {
  justify-content: center;
}

