@font-face {
  font-family: 'icomoon';
  src: url('fonts/icomoon.eot?x5v0cv');
  src: url('fonts/icomoon.eot?x5v0cv#iefix') format('embedded-opentype'),
    url('fonts/icomoon.ttf?x5v0cv') format('truetype'),
    url('fonts/icomoon.woff?x5v0cv') format('woff'),
    url('fonts/icomoon.svg?x5v0cv#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
