.pot-rate-item {
  position: relative;
  width: 2rem;
  flex: 0 1 auto;
  margin-right: 0.375rem;
}

.pot-rate-item::after {
  content: "";
  display: block;
  padding: 50% 0;
}

.pot-rate-item:last-child {
  margin-right: 0;
}

.pot-rate-item .pot-rate-item-def {
  position: absolute;
  width: 100%;
  height: 100%;
  background-size: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url("evaluation_icon_star_gray@2x.png");
}

@media (min-resolution: 3dppx) {
  .pot-rate-item .pot-rate-item-def {
    background-image: url("evaluation_icon_star_gray@3x.png");
  }
}

.pot-rate-item_active .pot-rate-item-def {
  background-image: url("evaluation_icon_star@2x.png");
}

@media (min-resolution: 3dppx) {
  .pot-rate-item_active .pot-rate-item-def {
    background-image: url("evaluation_icon_star@3x.png");
  }
}

