.pot-index-list {
  position: relative;
  height: 100%;
  overflow: hidden;
}

.pot-index-list .pot-scroll-wrapper {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
}

.pot-index-list-content {
  background: #fff;
  border-radius: 0.125rem;
}

.pot-index-list-title {
  padding: 0.875rem 1rem;
  font-size: 0.875rem;
  line-height: 1.6;
  color: #333;
}

.pot-index-list-anchor {
  padding: 1rem 1rem 0.625rem 1rem;
  line-height: 1;
  font-size: 0.875rem;
  color: #999;
  background: #f7f7f7;
}

.pot-index-list-fixed {
  z-index: 1;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.pot-index-list-nav {
  position: absolute;
  z-index: 30;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  font-family: Helvetica;
}

.pot-index-list-nav > ul {
  padding: 0;
  margin: 0;
}

.pot-index-list-nav > ul > li {
  padding: 0.375rem 1rem 0 1rem;
  line-height: 1;
  text-align: center;
  font-size: 0.75rem;
  color: #666;
}

.pot-index-list-nav > ul > li.active {
  color: #fc9153;
}

@media (max-height: 480px) {
  .pot-index-list-nav > ul > li {
    padding-top: 0.1875rem;
  }
}

