.pot-drawer-panel {
  position: relative;
  z-index: 1;
  flex: 1;
  width: 10.625rem;
  overflow: hidden;
  background-color: #fff;
  box-shadow: 0 0.0625rem 0.125rem rgba(0,0,0,0.2);
}

.pot-drawer-panel + .pot-drawer-panel {
  margin-left: -4.1875rem;
}

.pot-drawer-panel:first-child {
  box-shadow: none;
}

.pot-drawer-scroll-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.pot-drawer-move-enter,
.pot-drawer-move-leave-to {
  transform: translate(4.1875rem, 0);
}

.pot-drawer-move-enter-active,
.pot-drawer-move-leave-active {
  transition: transform 0.3s ease-in-out;
}

