.pot-drawer-item {
  padding: 0 1.25rem;
  height: 3.125rem;
  line-height: 3.125rem;
  white-space: nowrap;
  overflow: hidden;
  font-size: 0.9375rem;
}

.pot-drawer-item::after {
  left: 1.25rem;
}

.pot-drawer-item:last-child::after {
  display: none;
}

.pot-drawer-item:first-child::after {
  display: block;
}

.pot-drawer-item_active {
  background: rgba(0,0,0,0.04);
}

