.pot-upload-file {
  position: relative;
}

.pot-upload-file-def {
  position: relative;
  width: 5rem;
  height: 5rem;
  box-sizing: border-box;
  background: #fff no-repeat center;
  background-size: cover;
  border-radius: 0.125rem;
}

.pot-upload-file-def > .potic-wrong {
  position: absolute;
  z-index: 2;
  top: -0.125rem;
  right: -0.125rem;
  color: rgba(0,0,0,0.8);
  font-size: 1rem;
  background-color: #fff;
  border-radius: 50%;
}

.pot-upload-file-def > .potic-wrong::before {
  display: inline-block;
  transform: scale(1.625);
  transform-origin: center;
}

.pot-upload-file-state {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  opacity: 0;
  background-color: rgba(37,38,45,0.4);
  border-radius: 0.125rem;
  transition: opacity 0.1s;
}

.pot-upload-file-state::before {
  content: ".";
  position: relative;
  left: -50%;
  display: block;
  width: 0.0625rem;
  height: 0.0625rem;
  margin-left: -0.0625rem;
  background-color: rgba(0,0,0,0.1);
}

.pot-upload-file_stat {
  opacity: 1;
}

.pot-upload-file-status {
  position: relative;
  z-index: 1;
  font-size: 1.875rem;
  display: none;
}

.pot-upload-file-status.potic-right {
  display: block;
  color: #fc9153;
}

.pot-upload-file-status.potic-warn {
  display: block;
  color: #f43530;
}

.pot-upload-file-status.potic-right::after,
.pot-upload-file-status.potic-warn::after {
  content: "";
  z-index: -1;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 0.56em;
  height: 0.56em;
  border-radius: 50%;
  background-color: #fff;
}

.pot-upload-file-status.potic-right + .pot-upload-file-progress,
.pot-upload-file-status.potic-warn + .pot-upload-file-progress {
  display: none;
}

.pot-upload-file-progress {
  color: #fff;
  font-size: 1.25rem;
}

